<template>
  <div id="app">
    <link href="https://use.fontawesome.com/releases/v5.0.13/css/all.css" rel="stylesheet">

    <v-app>
 <v-card>
    <v-toolbar
      flat
      color="primary"
      dark
    >
      <v-toolbar-title>Restaurant Menagment App</v-toolbar-title>
    </v-toolbar>
    <v-tabs vertical>
      <v-tab>
        Add Waiters
      </v-tab>
      <v-tab>
        Add Tables
      </v-tab>
      <v-tab>
        Add Orders
      </v-tab>
      <!-- ADD WAITER TAB -->
      <v-tab-item>
        <v-card flat>
          <v-container grid-list-xs>
        <div class="d-flex"> 
          <v-text-field
              v-model="waiter"
              label="Add a waiter"
              @keydown.enter="addWaiter"
            ></v-text-field>
            <v-btn :disabled="!waiter" @click="addWaiter" color="success">Add Waiter</v-btn>
        </div>
           <v-snackbar
             v-model="infoMsg"
             :right="true"
             :top="true"
             color="error"
           >
           Please enter a Task
          </v-snackbar>
             
          <v-card
            elevation = 10
          >
            <v-card-title color="primary" :class="`d-flex justify-space-between`" >
             
                <label class="blue--text">Waiters: {{waiters.length}}</label>
             
              
            </v-card-title>
            <v-divider></v-divider>
            <v-list>
                <v-list-item  v-for="item in waiters" :key="item.id">
                  <v-list-item-content class="pl-3">
                    <v-list-item-action>
                       {{item.id}}
                    </v-list-item-action>
                      
                    
                  </v-list-item-content>
                  <v-list-item-icon>
                  <v-icon v-if="!item.isTaken" color="red" @click="deleteWaiter(item)">delete</v-icon>
                  <p v-else><i class="fas fa-ban"></i> Can't delte this waiter -> currently taken</p>
                  </v-list-item-icon>
                </v-list-item>
            </v-list>
             
            </v-card>
      </v-container>

        </v-card>
      </v-tab-item>
      <!-- ADD TABLE TAB -->
      <v-tab-item>
        <v-card flat>
                <v-card flat>
                  <v-container grid-list-xs>
                  <div class="d-flex"> 
                  <v-text-field
                      v-model="table"
                      label="Add a table"
                    ></v-text-field>
                    <v-text-field
                      v-model="tableY"
                      label="Add table Y coords"
                      counter="3"
                      maxlength="3"
                      hint="This field requires max 999 value"
                      type="number"
                       prefix="Y"
                      required
                    ></v-text-field>
                    <v-text-field
                      v-model="tableX"
                      label="Add table X coords"
                      @keydown.enter="addTable"
                      counter="3"
                      maxlength="3"
                       prefix="X"
                        type="number"
                      hint="This field requires max 999 value"
                    ></v-text-field>
                   
                    <v-btn :disabled="!tableX"  @click="addTable" color="success">Add Table</v-btn>
                  </div>
                    
                  
                  
                  
                  </v-container>
              </v-card>
          </v-card>
        </v-tab-item>

        <!-- ADD ORDER TAB -->
      <v-tab-item>
        <v-card flat>
                <v-card flat>
                  <v-container grid-list-xs>
                  <div> 
                  <v-text-field
                      v-model="orderName"
                      label="Name (anything)"
                    ></v-text-field>
                    <v-text-field
                      v-model="orderContent"
                      label="Order here:"
                      placeholder="2x Margharitta, 3x Coca-Cola, Water"
                    ></v-text-field>
                     <v-select
                    :items="freeWaiters"
                    v-model="orderWaiter"
                    item-text="id"
                    label="Select a waiter"
                    />
                    <v-select
                    :items="freeTables"
                    v-model="orderTable"
                    item-text="id"
                    label="Select a table"
                    />
                                
                    <v-btn :disabled="!orderName" @click="addOrder(orderTable, orderWaiter)" color="success">Add Order</v-btn>

                     <v-list>
                <v-list-item  v-for="order in orders" :key="order.id">
                  <v-list-item-content class="pl-3">
                    <v-list-item-action>
                       {{order.orderName}} table : {{order.orderTable}} waiter : {{order.orderWaiter}}
                    </v-list-item-action> 
                      
                    
                  </v-list-item-content>
                  <v-list-item-icon>
                  <v-icon color="red" @click="deleteOrder(order)">delete</v-icon>
                  </v-list-item-icon>
                </v-list-item>
            </v-list>
                  </div>
                    
                  
                  
                  
                  </v-container>
              </v-card>
          </v-card>
        </v-tab-item>
    </v-tabs>
  </v-card>


      
      <!--RESTAURANT VIEW-->
      <h2 style="text-align:center">Restaurant view</h2>
      <v-alert class="alert" :value="alert"
            type="success"
          >TABLE POSITION CHANGED SUCCESFULLY</v-alert>
      <div class="res-view" >
        
        <div class="tables">
          
          <div style="height: 1200px; width: 1400px; border: 1px solid red; position: relative;">
      
        <vue-draggable-resizable :onDragStart="onDragStartCallback"   drag-handle=".drag"  v-for="item in tables" :key="item.id" :x="item.tableX" :y="item.tableY" :resizable="false" class="table"  @dragging="onDrag" :parent="true">
          <div class="drag" style="display:flex;">DRAG ME 
            <v-btn @click="setNewPos(item, $event)" 
              class="success btn-notShow " style="width:100px"
              elevation="2" 
            ></v-btn>
        </div>
          <h2>Stolik : {{item.table}}</h2>
          <v-icon v-if="!item.isTaken" color="red" @click="deleteTable(item)">delete</v-icon>
         <p v-if="item.isTaken"><i class="fas fa-exclamation-triangle" style="font-size:25px; color:red"></i> This table is currenlty served by {{item.servedBy}} </p>
       
        </vue-draggable-resizable>
        
        <v-card
        elevation="2"
      ></v-card>
         
      </div>

      
        
         
        </div>
        
      </div>
        
  </v-app>
  </div>
</template>

<script>
import db from './firebase'
import firebase from 'firebase'
export default {
  name: 'App',
  components: {
  },
  data() {
    return {
      //waiters
      waiter:"",
      waiters:[],
      infoMsg:false,
      //table
      table:"",
      tableX : '',
      tableY : '',
      tables:[],
      //dragable
      top:"100px",
      isDragging :false,
      posChanged:false,
      // width: 0,
      // height: 0,
      x: 0,
      y: 0,
      alert:false,
      //orders:
      order:'',
      orders:[],
      orderName:'',
      orderContent : '',
      orderWaiter:'',
      orderTable:'',
      freeTables:[],
      freeWaiters:[],
      servedBy : '',
      takeTime:'',
    }
  },
  computed: {
    tasksDone(){
      return this.waiters.filter(todo => todo.isDone).length
    },
    progress(){
      return this.tasksDone / this.waiters.length * 100
    }
  },
  methods: {
    // onResize: function (x, y, width, height) {
    //   this.x = x
    //   this.y = y
    //   this.width = width
    //   this.height = height
    // },
   
    onDrag: function (x, y) {
      this.x = x
      this.y = y
    },
    onDragStartCallback(event){
      let draggedTable = event.target;
      let btn = draggedTable.querySelector('button');
      btn.innerHTML = "Set new";
      btn.style.display="block";
    },
  
    setNewPos: function(item,event){
      if(item){
      
        db.collection('tables')
          .doc(item.id)
          .update({
            tableY : this.y,
            tableX : this.x,
            createdAt: firebase.firestore.FieldValue.serverTimestamp()
          })

         let  el = event.target;
          el.style.display="none";
          this.alert=true;
           window.setInterval(() => {
        this.alert = false;
      }, 3000)  

      }
      
      
    },

  //   todoDone(todo){
    
  //  let isDone = todo.isDone ? true : false
  
  //  db.collection('waiters')
  //       .doc(todo.id)
  //       .update({ isDone })
     
  //   },
    //waiters
    addWaiter: function(){
      if(!this.waiter){
        this.infoMsg = true;
      }else{
     
      db.collection("waiters").add({
          todo: this.waiter,
          isTaken : false,
          createdAt: firebase.firestore.FieldValue.serverTimestamp()
      })   
      .catch(function(error) {
          console.error("Error adding collection: ", error);
      });
      this.waiter = "";
      }
    },
    deleteWaiter(item){
      db.collection('waiters')
        .doc(item.id)
        .delete()
    },
    deleteOrder(order){
       db.collection('waiters')
         .doc(order.orderWaiter)
          .update({
            isTaken : false
          })

        db.collection('tables')
          .doc(order.orderTable)
          .update({
            isTaken : false,
            orderID : ''
          })  


      db.collection('orders')
        .doc(order.id)
        .delete()

       
      
    },
    deleteTable(item){
      db.collection('tables')
        .doc(item.id)
        .delete()
    },
      addTable: function(){
      if(!this.table){
        this.infoMsg = true;
      }else{
     
      db.collection("tables").add({
          table: this.table,
          tableX: parseInt(this.tableX),
          tableY: parseInt(this.tableY),
          isTaken : false,
          createdAt: firebase.firestore.FieldValue.serverTimestamp()
      })   
      .catch(function(error) {
          console.error("Error adding collection: ", error);
      });
      
      this.table = "";
      this.tableX = "";
      this.tableY = "";
      }
    },
     //orders
      addOrder: function( orderTable,orderWaiter){
     
      db.collection("orders").add({
          orderName : this.orderName,
          orderContent:this.orderContent,
          orderWaiter : this.orderWaiter,
          orderTable : this.orderTable,
          createdAt: firebase.firestore.FieldValue.serverTimestamp(),
      })
      .then(function(docRef) {
        db.collection('tables')
          .doc(orderTable)
          .update({
            orderID : docRef.id
          })
      })   
      .catch(function(error) {
          console.error("Error adding collection: ", error);
      });
      this.waiter = "";
      db.collection('tables')
          .doc(orderTable)
          .update({
            isTaken : true,
            servedBy : orderWaiter,
            takeTime : firebase.firestore.FieldValue.serverTimestamp()
          })
      
        db.collection('waiters')
          .doc(orderWaiter)
          .update({
            isTaken : true
          })
        
    }
  },
 


  //tables

  firestore:{
    waiters: db.collection('waiters').orderBy('createdAt'),
    tables: db.collection('tables').orderBy('createdAt'),
    orders: db.collection('orders').orderBy('createdAt'),
    freeTables : db.collection("tables").where("isTaken", "==", false),
    freeWaiters : db.collection("waiters").where("isTaken", "==", false),
    
  }
 
}
</script>

<style scoped>
.res-view{
  background: rgb(207, 170, 170);
  width:1400px;
  margin:0 auto;
  height:1200px;
  
}
.tables{
  width:90%;
  position: relative;
}
.table{
  width:300px;
  height:200px;
  border:1px solid black;
  border-radius:5%;
  box-shadow:0 2px 2px 1px rgb(109, 108, 108);
  position:absolute;
  
}
.drag{
  cursor: move;
  background:gray;
  width:100%;
  height:25px;
  border-radius:10px 10px 0 0;
}
.btn-notShow{
  display: none;
}

</style>


